.wraplabel{
	margin: 4px 0;
}

.wraplabel .infoStyles{
  text-decoration: none;
  color: #000;
  cursor: pointer;

  font-size: 30px;

	background-image: -webkit-gradient(linear,left top, left bottom,color-stop(0, #1f133a),to(#1f133a));

	background-image: -webkit-linear-gradient(top,#1f133a 0,#1f133a 100%);

	background-image: -o-linear-gradient(top,#1f133a 0,#1f133a 100%);

	background-image: linear-gradient(to bottom,#1f133a 0,#1f133a 100%);
	background-size: 0px 3px;
	background-position: 0% 100%;
	background-repeat: no-repeat;
	-webkit-transition: all ease .5s;
	-o-transition: all ease .5s;
	transition: all ease .5s;
}

.wraplabel .infoStyles:hover{
	background-size: 100% 3px;
}


